import { Component, OnInit } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { ActivatedRoute } from '@angular/router';
import saveAs from 'save-as';
import * as Survey from 'survey-angular';
import * as json2csv from 'json2csv';

Survey.Survey.cssType = "bootstrap";

@Component({
  selector: 'app-results',
  template: `<div class="results">
    <h3>Résultats</h3>
    <div *ngFor="let value of data">
      <h5>{{ value.groupe }}</h5>
      <div>
        <strong class="title-line">Nombre de jeunes </strong><span>{{ value.jeunes.length }}</span>
      </div>
      <div>
        <strong class="title-line">Nombre de chefs </strong><span>{{ value.chefs.length }}</span>
      </div>
      <div>
        <strong class="title-line">Régime </strong><div class="indent">{{ value['régime'] }}</div>
      </div>
      <div>
        <strong class="title-line">Commentaire </strong><div class="indent">{{ value['comment'] }}</div>
      </div>
    </div>

    <h5>Jeunes <button class="btn btn-primary pull-right" (click)="saveJeunesAsCvs()">Télécharger CSV</button> </h5>
    <table class="table">
      <tr>
        <th>Nom</th>
        <th>Groupe</th>
        <th>Responsabilité</th>
        <th>Année</th>
      </tr>
      <tr *ngFor="let jeune of jeunes">
        <td>{{ jeune.nom }}</td>
        <td>{{ jeune.groupe }}</td>
        <td>{{ jeune.responsabilite }}</td>
        <td>{{ jeune.annee }}</td>
      </tr>
    </table>

    <h5>Chefs <button class="btn btn-primary pull-right" (click)="saveChefsAsCvs()">Télécharger CSV</button></h5>
    <table class="table">
      <tr>
        <th>Nom</th>
        <th>Groupe</th>
        <th>Email</th>
        <th>Je participe à</th>
      </tr>
      <tr *ngFor="let chef of chefs">
        <td>{{ chef.nom }}</td>
        <td>{{ chef.groupe }}</td>
        <td>{{ chef['Adresse email'] }}</td>
        <td>{{ chef["Je participe à l'annimation"] }}</td>
      </tr>
    </table>
  </div>`
})
export class ResultsComponent implements OnInit {
  public data: any = null;
  public resultCount = 0;
  public jeunes: any[] = [];
  public chefs: any[] = [];

  private key      = '068282521c71483eadef7d00a8b82615';
  private survey   = '181f389e-1ba7-4745-a903-28ca94f01396';

  constructor(private http: HttpClient) {}

  ngOnInit(): void {
    this.http.get(`https://b219.guyard.fr/api/MySurveys/getSurveyResults/${this.survey}?accessKey=${this.key}`, {
      headers: new HttpHeaders().set('X-Proxy-To', 'surveyjs.io')
    }).subscribe(data => {
      this.data = data['Data'];
      this.resultCount = data['ResultCount'];
      this.jeunes = [];
      this.chefs  = [];

      for (let i = 0; i < this.data.length; ++i) {
        const obj = this.data[i];
        obj.jeunes.map(j => ({
            ...j, groupe: obj.groupe
          })
        ).forEach(e => {
          this.jeunes.push(e);
        });

        obj.chefs.map(j => ({
            ...j,
            groupe: obj.groupe
          })
        ).forEach(e => {
          this.chefs.push(e);
        });
      }
    });
  }

  public saveChefsAsCvs() {
    const fields = ['nom', 'groupe', 'Adresse email', 'Je participe à l\'annimation'];

    try {
      var result = json2csv({ data: this.chefs, fields: fields });
      console.log(result);
      let blob = new Blob([result], { type: 'text/csv;charset=utf-8' })
      saveAs(blob, 'resultats.csv');
    } catch (err) {
      // Errors are thrown for bad options, or if the data is empty and no fields are provided.
      // Be sure to provide fields if it is possible that your data array will be empty.
      console.error(err);
    }
  }

  public saveJeunesAsCvs() {
    const fields = ['nom', 'groupe', 'responsabilite', 'annee'];

    try {
      var result = json2csv({ data: this.jeunes, fields: fields });
      console.log(result);
      let blob = new Blob([result], { type: 'text/csv;charset=utf-8' })
      saveAs(blob, 'resultats.csv');
    } catch (err) {
      // Errors are thrown for bad options, or if the data is empty and no fields are provided.
      // Be sure to provide fields if it is possible that your data array will be empty.
      console.error(err);
    }
  }
}



